<template>
<div class="center">
   <section class="head">
      <div> <a @click="goBack" class="iconfont back"> &#xe614; </a> </div>
      <h1>Add Bank Card</h1>
       <div class="chartWrap">
        </div>
    </section>
    <section class="inputBox">
        <van-cell-group>
            <van-field reuired="true" v-model="card.cardNum" placeholder="Card NO." />
            <van-field reuired="true" v-model="card.termValidity" placeholder="Term of Validity(Month-Year)" />
            <van-field reuired="true" v-model="card.securityCode" placeholder="CSC / CVV / CVC (3 or 4-digit on the card's back)" />
            <van-field reuired="true" v-model="card.name" placeholder="Name on Card" />
        </van-cell-group>
        <div class="choice">
          <van-checkbox checked-color="#e33946" v-model="checked">Save card NO.(Not include CSC / CVV / CVC )</van-checkbox>
        </div>
    </section>  
    <section class="btnWrap">
        <van-button type="danger" @click="submitForm">Add</van-button>
    </section>
  </div>
</template>

<script>

import { addCard } from "@/api/bankCard";
export default {
  name: 'addBankcard',
  data () {
    return {
      checked: false ,
      card:{
          cardNum:"",
          name:"",
          termValidity:"",
          securityCode:""
      }
    
    }
  },
  computed: {
  },
  mounted() {
    this.init();
  },
  methods: {
    init(){

    },
    changeSort(val){
      this.sortValue=val;
      this.sortVisible=false;
    },
    goBack(){
      this.$router.back(-1)
    },
    submitForm(){
      addCard(this.card).then(res=>{
          console.log(1234)
          this.$toast('Card added successfully')
          this.$router.push('myBankcards')
      });
    }


  }
}
</script>
<style lang="scss">
  .choice .van-checkbox__label{
    color: #666666 !important;
  }
</style>

<style lang="scss" scoped>
    .head{
        padding: 0 20px 0 10px;
        background-color: #fff;
        margin-bottom: 10px;
    }
    .center{
        background:rgba(246,246,246,1);
    }
    .btnWrap{
        background:rgba(246,246,246,1);
        height: 373px;
    }
    .btnWrap .van-button--danger{
        width: 90%;
        margin-left: 5%;
        margin-top: 100px;
    }
    .choice{
      width: 100%;
      padding: 10px 4%;
      font-size: 12px;
    }
    .inputBox{
      .van-cell{
        padding: 13px 15px;
      }
    }
</style>



